// superfish menu

$sfmenu-item-color: $color-text;
$sfmenu-item-hover-color: #59443F;;
$sfmenu-item-bg-color: $color-2;
$sfmenu-item-hover-bg-color: #59443F;
$sfmenu-triangle-color: $color-text;
$sfmenu-triangle-hover-color: #fff;
$menu-dropdown-item-color: #fff;
$sub-menu-bg-color: #59443F;

.menu-wrap {
    display: none;

    @include media(">=md") {
        display: flex;
        justify-content: space-between;
    }
}


.sf-menu {
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-left: -15px;
    margin-right: -15px;
    width: calc(100% + 30px);
    @include list-unstyled;

    @include media(">=lg") {
        margin-right: 0;
        width: auto;
        margin-left: 135px;
        margin-top: 75px;
    }
    @include media ('>=xl'){
        
        margin-left: 235px;
    } 

    li {
        display: block;
        position: relative;
    }

    a {
        display: flex;
        position: relative;        
        text-transform: uppercase;
        text-decoration: none !important;
        color: $menu-dropdown-item-color;
        font-family: $font-futura;
        font-weight: 700;
        font-style: normal;
        font-size: 20px;
    color: #C3B9B5;;    
        line-height: 20px;
        @include media ('>=lg'){
            font-size: 16px;
        }
        @include media ('>=xl'){
            font-size: 20px;
        }
    }

    // Верхний уровень меню
    > li {
        margin: 0 1px 0 0;
        ul {
            //border-top: 2px $color-2 solid;
        }

        > a {
            transition: background-color 0.2s ease;
            position: relative;
            height: 48px;
            margin-right: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: $sfmenu-item-color;
            @include media ('>=lg'){
                margin-right: 40px;
            }
            @include media ('>=xl'){
                margin-right: 80px;
            }
            
        }
        
        // активный элемент - верхнего уровня
        &.active,
        &.current-menu-item,
        &.current-menu-parent,
        &.current-menu-ancestor {
            > a {
                background-color: $sfmenu-item-hover-bg-color;
            }
        }
        
        & > a:hover,
        &.sfHover > a {
            color: #59443F;
            text-decoration: none;
        }
    }

    
    // Субменю всех уровней
    .sub-menu {
        margin: 0;
        position: absolute;
        display: none;
        top: 100%;
        left: 0;
        z-index: 9999;
        background-color: $sub-menu-bg-color;
        min-width: 20em;
        padding-top: 10px;
        padding-bottom: 10px;
        box-shadow: 0 7px 8px -2px rgba(12, 12, 12, 0.2);
        padding-left: 0;

        li {
            width: 100%;
            position: relative;
            display: block;
            margin-bottom: 1px;

            a {
                line-height: 2.5;
                text-align: left;
                padding: 12px 15px;
                line-height: 1.3;
                font-size: rem(13px);
                font-style: normal;
                font-weight: 600;
            }
        }

        > li:hover > a,
        > li.current-menu-item > a,
        > li.current-menu-ancestor > a {
            //background-color: rgba(0, 0, 0, 0.15);
            background-color: darken($color-2, 3%);
            color: $sfmenu-item-hover-color;
        }
    }

    
        
    // Субменю второго уровня
    > li > .sub-menu {
        // Треугольник над выпадающим меню ( только для второго уровня )
        &:after {
            content: "";
            position: absolute;
            z-index: 9999;
            left: 15px;
            bottom: 100%;
            border: 6px solid transparent;
            border-bottom: 6px solid $sfmenu-item-bg-color;
        }
    }


    //  Субменю 3 и более уровеня
    .sub-menu .sub-menu {
        top: auto;
        bottom: 0;
        left: 100%;
        
        &:after {
            display: none;
        }
    }


    // Стрелочки ссылки есть есть субменю
    a.sf-with-ul {
        padding-right: 29px !important;

        &:after {
            font-family: "icons";
            content: "c";
            color: $sfmenu-triangle-color;
            font-size: 18px;
            position: absolute;
            right: 7px;
            top: 50%;
            transform: translateY(-50%);
            text-transform: lowercase;
        }

    }
    
    .sub-menu a.sf-with-ul{
        &:after {
            content: "e";
        }
    }

}
.city{
    font-family: $font-futura;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #FFC59E;
    text-decoration: none;
    margin-left: 35px;
    position: relative;
    margin-top: 100px;
    &::before{
        position: absolute;
        content:'\e800';
        font-family: 'my__icon';
        top: 0;
        left: -39px;
    }
    @include media ('>=lg'){
        font-size: 20px;
    }
    @include media ('>=xl'){
        font-size: 20px;
        margin-left: 285px;
    }
}