.about {
    background: #FFFEFE;
    padding: 80px 0;
    @include media ('>=lg'){
        padding-bottom: 309px;
    }
    &__title {
        font-family: $font-school;
        font-size: 40px;
        line-height: 40px;
        font-weight: 400;
        color: #080606;
        text-align: center;
        @include media ('>=lg'){
            margin-bottom: 104px;
        }
    }
&__inner{
    margin-left: 30px;
    display: flex;
    flex-wrap: wrap;
}
&__item{
    margin-left: 40px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: start;
    flex-wrap: wrap;
    width: 320px;
    @include media ('>=lg'){
        margin-bottom: 104px;
    }
    @include media ('>=xl'){
        margin-bottom: 130px;
        margin-left: 94px;
    }
}
&__text{
    order: 1;
    font-size: 20px;
    line-height: 30px;
    font-family: $font-futura;
    font-weight: 400;
    color: #59443F;
}
    &__item h4 {
        font-family: $font-school;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 30px;
        color: #080606;
        align-self: center;
        margin-top: 20px;
        @include media ('>=sm'){
            font-size: 40px;
        }
       
    }
    &__wrap{
        justify-content: center;
        display: flex;
        flex-wrap: wrap;
    }
}
